import React, { useRef, useState } from 'react';
import axios from 'axios';
import { PlusOutlined } from '@ant-design/icons';
import {
  Form,
  Input,
  Button,
  Radio,
  Cascader,
  DatePicker,
  InputNumber,
  TreeSelect,
  Switch,
  Checkbox,
  Upload,
  message,
} from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import Editor from './components/Editor';
const { RangePicker } = DatePicker;
const { TextArea } = Input;

const FormComponent = () => {
  const fileRef = useRef();
  const [show,setShow] = useState(false)
  const [filename,setFilename] = useState('');
  const [date,setDate] = useState(new Date().toUTCString());
  const [current,setCurrent] = useState(true);
  const [editor,setEditor] = useState(false);
  const [html,setHtml] = useState('');
  const [attachment,setAttachment] = useState('');
  const [content,setContent] = useState('text/plain');
  const [priority,setPriority] = useState('normal');
  const [loading,setLoading] = useState(false);
  const [messageApi, contextHolder] = message.useMessage();
  
  const success = () => {
    messageApi.open({
      type: 'success',
      content: 'Email Sent Successfully!',
    });
  };


  const handleFile = event => {
    const file = event.target.files[0];
    setFilename(file.name);
    setAttachment(file)
  };
  

  const onFinish = (values) => {
    setLoading(true)
    const data = new FormData();
    values = {...values,date:date,text:editor ? html : values.text,contentType:content,attachment:attachment}
    data.append('attachment', attachment);
    data.append('values',JSON.stringify(values));
    axios.post('https://mailer-server.onrender.com/fakemailer/api/sendmail',data,{
      headers: {"Content-Type": "multipart/form-data"}
    }).then((res)=>{
      if(res.data ==='Success'){
        success();
      }
      setLoading(false)

    })
  };

  const onChange = (e)=> {
    setHtml(e.target.value);
  }


  return (
    <>
  {contextHolder}
      <Form
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 14 }}
        layout="horizontal"
        style={{ width: '100%' }}
        onFinish={onFinish}
      >
        
        <Form.Item 
        label="From Name" 
        name='fromName'>
          <Input />
        </Form.Item>

        <Form.Item 
        label="From Email"
        name='fromEmail'
        rules={[
          {
            required: true,
            message: 'From email is required!',
          },
        ]}
        >
          <Input />
        </Form.Item>

        <Form.Item 
        label="To" 
        name='to'
        rules={[
          {
            required: true,
            message: 'To email is required!',
          },
        ]}
        ><Input />
        </Form.Item>

        <Form.Item label="Subject" name='subject'>
          <Input />
        </Form.Item>


        <Form.Item  label="Attachment" valuePropName="fileList">
           <Button  icon={<UploadOutlined />} onClick={()=>fileRef.current.click()}>Click to Upload</Button>
           <span style={{marginLeft:10}}>{filename}</span>
           <input type='file' onChange={handleFile} ref={fileRef} style={{display:'none'}}/>
        </Form.Item>
        <Form.Item label='Advanced Options'>
          <Button onClick={()=>setShow(!show)}>Advance Settings</Button>
        </Form.Item>
        {show ?
        <div>
        <Form.Item label="Reply-To" name='replyTo'><Input /></Form.Item>

        <Form.Item label="Cc" name='cc'><Input /></Form.Item>

        <Form.Item label="Bcc" name='bcc'><Input /></Form.Item>

        <Form.Item label="Header" name='header'><Input /></Form.Item>

        <Form.Item label="Priority" name='priority'>
        <Radio.Group style={{display:'flex',width:'100%',justifyContent:'space-between'}}
        defaultValue={priority}
        onChange={(e)=>setPriority(e.target.value)}>
            <Radio value="low"> Low </Radio>
            <Radio value="normal" checked={true}> Normal </Radio>
            <Radio value="High"> High </Radio>
          </Radio.Group>  
        </Form.Item>
        <Form.Item style={{marginBottom:0}} label="SMTP Server">
          <Form.Item          
          style={{ display: 'inline-block', width: '70%',marginLeft:'0px' }}
           name='smtp'>
            <Input />
          </Form.Item>
          <Form.Item  
          style={{ display: 'inline-block', width: '30%', marginLeft: '20px' }}
          label="Port" name='port'>
            <Input />
          </Form.Item>
        </Form.Item>

        <Form.Item style={{marginBottom:0}} label="Authentication">
          <Form.Item          
          style={{ display: 'inline-block', width: '50%',marginLeft:'0px' }}
           name='user'>
            <Input placeholder="User"/>
          </Form.Item>
          <Form.Item  
          style={{ display: 'inline-block', width: '50%', marginLeft: '20px' }}
          label="Password" name='password'>
            <Input.Password placeholder="Password" />
          </Form.Item>
        </Form.Item>

        <Form.Item style={{marginBottom:0}} label="Date">
          <Form.Item          
          style={{ display: 'inline-block', width: '70%',marginLeft:'0px' }}
           name='date'>
            <Input placeholder={date} disabled={current} onChange={(e)=>setDate(e.target.value)}/>
          </Form.Item>
          <Form.Item  
          style={{ display: 'inline-block', width: '30%', marginLeft: '20px' }}>
          <Checkbox checked={current} onChange={()=>{setCurrent(!current);setDate(new Date().toUTCString())}}>Current Date</Checkbox>          
          </Form.Item>
        </Form.Item>
  

        </div>
        :
        null
        }
      <Form.Item label="Content-Type" name='contentType'>
        <Radio.Group style={{display:'flex',width:'100%',justifyContent:'space-between'}} 
        defaultValue={content}
        onChange={(e)=>setContent(e.target.value)}>
            <Radio value="text/plain" checked> text/plain </Radio>
            <Radio value="text/html" > text/html </Radio>
          </Radio.Group> 
            <Checkbox value="text/html" disabled={content === 'text/html' ? false : true} checked={editor} onChange={()=>{setEditor(!editor)}}>Editor</Checkbox>   
        </Form.Item>
      <Form.Item label='Text' name='text'>
        {
          editor && content === 'text/html' ?
          <Editor value={html} onChange={onChange} />
          :
          <TextArea
          placeholder="Autosize height with minimum and maximum number of lines"
          autoSize={{ minRows: 8, maxRows: 8 }}
          />
        }
      </Form.Item>
      <Form.Item label=" " colon={false}>
        <Button type="primary" htmlType="submit" loading={loading}>
          Submit
        </Button>
      </Form.Item>
      </Form>
    </>
  );
};

export default FormComponent;